.wrapper {
  display: grid;
  grid-auto-rows: minmax(1fr, 300px);
  grid-template-columns: repeat(8, 1fr);
  margin-top: 5px;
  -ms-flex-pack: stretch;
      justify-content: stretch;
}

.grid-item {
  grid-column: 3 / span 5;
  background-color: #576f75;
  color: #e3e3e3;
  padding: 20px;
}

.text-center {
  text-align: center;
}



