.navbar {
  margin: 0;
  background-color: #576f75;
  border: 4px solid #6f888a;
}

.nav-container {
  display: -ms-flexbox;
  display: flex;
  list-style-type: none;
  -ms-flex-align: baseline;
      align-items: baseline;
  -ms-flex-pack: center;
      justify-content: center;
}

@media (max-width: 500px) {
  .nav-container {
    -ms-flex-direction: column;
        flex-direction: column;
  }
  .posts,
  .resources,
  .about,
  .contact {
    font-size: 10px;
    padding: 2px;
  }
}

.nav-item {
  padding: 5px;
}

.posts,
.resources,
.about,
.contact {
  font-size: 24px;
  color: #55c500;
  text-decoration: none;
  text-shadow: 1px 1px 0 black;
}

.logo {
  max-height: 50px;
}

.home {
  -ms-flex: 1 1;
      flex: 1 1;
}


