.container {
  display: grid;
}

.post-preview-wrapper {
  display: grid;
  text-decoration: none;
  grid-column: 1;
  grid-row: 1 / span 5;
  grid-template-rows: 100px 200px;
}

.post-preview-wrapper:hover .title-container{
  opacity: .9;
  -webkit-box-shadow: 0px 0px 1px 3px #439b00 inset, 0px 0px 1px 8px #4fbd01 inset;
          box-shadow: 0px 0px 1px 3px #439b00 inset, 0px 0px 1px 8px #4fbd01 inset;
}

.post-preview-wrapper:hover img{
  -webkit-filter: brightness(66%);
          filter: brightness(66%);
}

.title-container {

  -ms-flex-align: center;

      align-items: center;
  margin-top: 5px;
  margin-left: 5px;
  margin-right: 5px;
  background-color: #55c500;
  color: #001f3f;
  border-bottom: 4px solid #001f3f;
}

.title {
  text-align: center;
}

.preview-image-container {
  display: grid;
  justify-items: center;
}

.preview-img {
  max-height: 98%;
  min-height: 94%;
  width: 95%;
  margin-bottom: 5px;
}

.save-post {
  grid-column: 1;
  grid-row: 5;
  margin: 8px;
  color: #001f3f
}

.save-button {
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit;
}

.save-logo {
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
}



