body {
  margin: 0;
  padding: 0;
  background-color: #364549;
}

.posts-wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  background-color: #576f75;
  margin-top: 5px;
}
@media(min-width: 350px){
  .posts-wrapper {
    margin-left: 50px;
    margin-right: 50px;
  }
}


